<template>
  <div class="image-uploader">
    <div class="uploader">
      <div 
        class="upload-area"
        @dragover.prevent
        @dragenter.prevent
        @drop.prevent="handleDrop"
        @click="triggerFileInput"
        :class="{ disabled: loading, 'drag-over': dragOver }"
        @dragenter="dragOver = true"
        @dragleave="dragOver = false"
      >
        <input 
          ref="fileInput"
          type="file"
          accept="image/*"
          @change="handleFileInput"
          style="display: none"
          :disabled="loading"
        >
        
        <div v-if="imageUrl" class="image-preview">
          <img :src="imageUrl" class="preview-image">
          <div class="re-upload-mask">重新上传</div>
        </div>
        
        <div v-else class="upload-placeholder">
          <span class="upload-icon">📤</span>
          <div class="upload-text">将图片拖到此处，或<em>点击上传</em></div>
          <div class="upload-tip">支持 JPG/PNG/GIF/WEBP 格式，大小不超过10MB</div>
        </div>
        
        <div class="upload-loading" v-if="loading">
          <div class="loading-spinner"></div>
          <p>分析中...</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/**
 * @description 图片上传组件
 */

export default {
  name: 'ImageUploader',
  data() {
    return {
      loading: false,
      imageUrl: null,
      dragOver: false
    };
  },
  methods: {
    triggerFileInput() {
      if (!this.loading) {
        this.$refs.fileInput.click();
      }
    },
    
    handleFileInput(event) {
      const file = event.target.files[0];
      if (file) {
        this.handleFile(file);
      }
    },
    
    handleDrop(event) {
      this.dragOver = false;
      const files = event.dataTransfer.files;
      if (files.length > 0) {
        this.handleFile(files[0]);
      }
    },
    
    async handleFile(file) {
      if (!this.validateFile(file)) {
        return;
      }
      
      this.loading = true;
      this.imageUrl = URL.createObjectURL(file);
      
      try {
        await this.analyzeImage(file);
      } catch (error) {
        this.handleError(error);
      }
    },
    
    validateFile(file) {
      const isImage = file.type.startsWith('image/');
      const isLt10M = file.size / 1024 / 1024 < 10;

      if (!isImage) {
        this.showMessage('只能上传图片文件！', 'error');
        return false;
      }
      if (!isLt10M) {
        this.showMessage('上传图片大小不能超过 10MB！', 'error');
        return false;
      }
      
      return true;
    },
    
    showMessage(message, type = 'info') {
      // 简单的消息提示实现
      const messageEl = document.createElement('div');
      messageEl.className = `custom-message custom-message-${type}`;
      messageEl.textContent = message;
      
      Object.assign(messageEl.style, {
        position: 'fixed',
        top: '20px',
        left: '50%',
        transform: 'translateX(-50%)',
        padding: '12px 20px',
        borderRadius: '6px',
        color: 'white',
        fontSize: '14px',
        zIndex: '9999',
        transition: 'all 0.3s ease'
      });
      
      if (type === 'error') {
        messageEl.style.backgroundColor = '#F56C6C';
      } else if (type === 'success') {
        messageEl.style.backgroundColor = '#67C23A';
      } else {
        messageEl.style.backgroundColor = '#409EFF';
      }
      
      document.body.appendChild(messageEl);
      
      setTimeout(() => {
        messageEl.style.opacity = '0';
        setTimeout(() => {
          document.body.removeChild(messageEl);
        }, 300);
      }, 3000);
    },
    
    async analyzeImage(file) {
      console.log('开始分析图片:', {
        fileName: file.name,
        fileSize: file.size,
        fileType: file.type
      });
      
      try {
        // 将图片转换为base64
        const base64 = await this.fileToBase64(file);
        
        // 直接调用大模型API
        const analysisResult = await this.callAIModel(base64);
        
        console.log('分析成功:', analysisResult);
        this.handleSuccess({ analysis: analysisResult });
        
      } catch (error) {
        console.error('分析失败详情:', {
          message: error.message
        });
        
        throw error;
      }
    },
    
    async fileToBase64(file) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.onload = () => resolve(reader.result);
        reader.onerror = reject;
        reader.readAsDataURL(file);
      });
    },
    
    async callAIModel(imageBase64) {
      const apiKey = '161ec8aa-2cb6-4c30-add2-e34ec5e0a94b';
      const baseUrl = 'https://ark.cn-beijing.volces.com/api/v3';
      const modelId = 'doubao-1-5-thinking-vision-pro-250428';
      
      const prompt = `请严格按以下结构化格式（使用中文）分析图片并输出：

【服装分析】
- 整体概述：2-3句描述风格/氛围
- 单品清单：逐条列出"明确名称：版型，材质/纹理，主色，图案，季节"，避免泛化如"上衣/裤子"，应写"白色直筒牛仔裤/米色羊毛西装外套"等
- 场景识别：是否为"酒吧/咖啡厅/街头/海边/室内办公/校园"等
- 场景即时建议：在不改动主体服装的前提下，追加2-3条"配饰/层次/鞋履/外搭"的优化建议。

【穿搭建议】
- 人物特征：性别特征、年龄感、体型轮廓、气质、肤色倾向
- 分场景推荐（只推荐3个：日常办公/约会/商务会议/朋友聚会/颁奖典礼/逛街购物/海滩度假/旅行/婚礼等任意3个）：
  1) 服装组合（上装/下装/外套/鞋履），明确"名称+颜色+关键版型"
  2) 相配首饰、帽子与配件（2-3项）
  3) 适用理由与风格效果（1-2句）
- 配色与层次：整体配色方案与层次结构

[结构化数组输出]
- CLOTHES_IN_IMAGE=["白色直筒牛仔裤","米色羊毛西装外套",...]
- OUTFIT_SCENES={
  "通勤":["米色羊毛西装外套","白色真丝衬衫","灰色西装裤","黑色乐福鞋"],
  "约会":[...],
  "度假":[...]
}
注意：以上两项为机器可读，放在文末，除此之外不要再输出其他JSON。`;

      try {
        const response = await fetch(`${baseUrl}/chat/completions`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${apiKey}`
          },
          body: JSON.stringify({
            model: modelId,
            messages: [
              {
                role: 'user',
                content: [
                  {
                    type: 'image_url',
                    image_url: {
                      url: imageBase64
                    }
                  },
                  {
                    type: 'text',
                    text: prompt
                  }
                ]
              }
            ]
          })
        });
        
        if (!response.ok) {
          throw new Error(`API调用失败: ${response.status}`);
        }
        
        const data = await response.json();
        const content = data.choices[0].message.content;
        
        // 提取并打印数组到控制台
        this.extractAndLogArrays(content);
        
        return {
          clothing_type: this.extractClothingType(content),
          style_tags: this.extractStyleTags(content),
          color_tags: this.extractColorTags(content),
          detailed_analysis: content,
          raw_response: content
        };
        
      } catch (error) {
        console.error('AI模型调用失败:', error);
        throw new Error(`AI分析失败: ${error.message}`);
      }
    },
    
    extractClothingType(content) {
      // 简单提取服装类型
      if (content.includes('上衣') || content.includes('T恤') || content.includes('衬衫')) return '上衣';
      if (content.includes('裤子') || content.includes('牛仔裤')) return '裤子';
      if (content.includes('裙子') || content.includes('连衣裙')) return '裙装';
      return '服装';
    },
    
    extractStyleTags(content) {
      const styles = [];
      const styleKeywords = ['休闲', '正式', '运动', '时尚', '简约', '复古', '甜美', '帅气', '优雅', '商务'];
      styleKeywords.forEach(style => {
        if (content.includes(style)) styles.push(style);
      });
      return styles.slice(0, 5);
    },
    
    extractAndLogArrays(content) {
      try {
        // 提取CLOTHES_IN_IMAGE数组
        const clothesMatch = content.match(/CLOTHES_IN_IMAGE\s*=\s*\[(.*?)\]/);
        if (clothesMatch) {
          const clothesArray = clothesMatch[1].split(',').map(item => item.trim().replace(/['"]/g, ''));
          console.log('CLOTHES_IN_IMAGE:', clothesArray);
        }
        
        // 提取OUTFIT_SCENES对象
        const outfitMatch = content.match(/OUTFIT_SCENES\s*=\s*\{([\s\S]*?)\}/);
        if (outfitMatch) {
          const outfitText = outfitMatch[1];
          const scenes = {};
          
          // 匹配每个场景
          const sceneMatches = outfitText.match(/"([^"]+)"\s*:\s*\[(.*?)\]/g);
          if (sceneMatches) {
            sceneMatches.forEach(match => {
              const sceneMatch = match.match(/"([^"]+)"\s*:\s*\[(.*?)\]/);
              if (sceneMatch) {
                const sceneName = sceneMatch[1];
                const items = sceneMatch[2].split(',').map(item => item.trim().replace(/['"]/g, ''));
                scenes[sceneName] = items;
              }
            });
          }
          
          console.log('OUTFIT_SCENES:', scenes);
        }
      } catch (error) {
        console.error('提取数组时出错:', error);
      }
    },

    extractColorTags(content) {
      const colors = [];
      const colorKeywords = ['白色', '黑色', '蓝色', '红色', '绿色', '黄色', '灰色', '棕色', '粉色', '紫色'];
      colorKeywords.forEach(color => {
        if (content.includes(color)) colors.push(color);
      });
      return colors.slice(0, 5);
    },
    
    handleSuccess(response) {
      this.loading = false;
      if (response && response.analysis) {
        this.$emit('upload-success', response.analysis);
        this.showMessage('图片分析完成！', 'success');
      } else {
        this.handleError(new Error('分析响应格式不正确'));
      }
    },
    
    handleError(error) {
      this.loading = false;
      this.imageUrl = null;
      
      let message = '上传失败，请稍后重试';
      
      if (error.response) {
        const status = error.response.status;
        const data = error.response.data;
        
        switch (status) {
          case 400:
            message = data?.detail || data?.message || '请求参数错误';
            break;
          case 401:
            message = '未授权，请重新登录';
            break;
          case 413:
            message = '文件过大';
            break;
          case 415:
            message = '不支持的文件格式';
            break;
          case 500:
            message = '服务器内部错误';
            break;
          default:
            message = `上传失败 (${status})`;
        }
      } else if (error.code === 'ECONNABORTED') {
        message = '上传超时，请检查网络连接';
      }
      
      this.showMessage(message, 'error');
    }
  }
};
</script>

<style lang="scss" scoped>
