  display: flex;
  justify-content: flex-start;
  padding-left: 30px;
  
  .uploader {
    width: 400px;
    
    .upload-area {
      width: 400px;
      height: 600px;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      background-color: rgba(255, 255, 255, 0.05);
      border: 2px dashed rgba(255, 255, 255, 0.2);
      border-radius: 10px;
      position: relative;
      overflow: hidden;
      transition: all 0.3s ease;
      cursor: pointer;
      
      &:hover, &.drag-over {
        border-color: #3A7AFE;
        background-color: rgba(58, 122, 254, 0.1);
      }
      
      &.disabled {
        cursor: not-allowed;
        opacity: 0.6;
      }
    }
  }
}

.upload-placeholder {
  text-align: center;
  color: #9aa4b2;
  
  .upload-icon {
    font-size: 60px;
    margin-bottom: 20px;
    display: block;
  }
  
  .upload-text {
    font-size: 18px;
    margin-bottom: 10px;
    
    em {
      color: #3A7AFE;
      font-style: normal;
    }
  }
  
  .upload-tip {
    font-size: 14px;
    margin-top: 15px;
  }
}

.image-preview {
  width: 100%;
  height: 100%;
  position: relative;
  .preview-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  .re-upload-mask {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    opacity: 0;
    transition: opacity 0.3s;
  }
  &:hover .re-upload-mask {
    opacity: 1;
  }
}

.upload-loading {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 10;
  
  .loading-spinner {
    width: 40px;
    height: 40px;
    border: 3px solid #f3f3f3;
    border-top: 3px solid #3498db;
    border-radius: 50%;
    animation: spin 1s linear infinite;
    margin-bottom: 15px;
  }
  
  p {
    margin: 0;
    font-size: 16px;
  }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
